import React, { useEffect, useState } from "react";
import { ToastContainer } from "react-toastify";
import Loader from "../../../../common/loader/Loader";
import CloseIcon from "@mui/icons-material/Close";
import IconButton from "@mui/material/IconButton";
import LockIcon from "@mui/icons-material/Lock";
import CurrencyRupeeIcon from "@mui/icons-material/CurrencyRupee";
import { message } from "antd";

import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  InputAdornment,
  Stack,
  TextField,
  Box,
  Button,
  FormControl,
  InputLabel,
  MenuItem,
  Pagination,
  Paper,
  Select,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tabs,
  Typography,
  Autocomplete,
} from "@mui/material";
import {
  getAsignReqPackage,
  AsignPackageToRetailer,
  DistributorPurchasePackageList,
} from "../../../../api/login/Login";
import toastMessage from "../../../../utils/toastMessage";

const DetailRow = ({ label, value }) => {
  return (
    <Stack
      borderBottom={1}
      borderColor={"#F4F0F0"}
      direction={"row"}
      width={"100%"}
    >
      <Typography
        variant="h6"
        sx={{ flex: 1, textAlign: "left", marginLeft: "10px" }}
        style={{ fontSize: "14px", color: "var(--bs-nav)" }}
      >
        {label} :
      </Typography>
      <Typography
        variant="h6"
        sx={{ flex: 1, textAlign: "right", marginRight: "10px" }}
        style={{ fontSize: "14px", color: "var(--bs-nav)", opacity: "0.8" }}
      >
        {value}
      </Typography>
    </Stack>
  );
};

const dialogInputSx = {
  "& .MuiOutlinedInput-root": {
    height: 42,
    backgroundColor: "#fff",
    borderRadius: "8px",
    transition: "all 0.2s ease",

    "& fieldset": {
      borderColor: "rgba(0,0,0,0.25)",
    },

    "&:hover fieldset": {
      borderColor: "var(--bs-primary1)",
    },

    "&.Mui-focused fieldset": {
      borderColor: "var(--bs-primary1)",
      borderWidth: "1.5px",
    },
  },

  "& label.Mui-focused": {
    color: "var(--bs-primary1)",
  },
};

const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    fullWidth
    sx={dialogInputSx}
    {...props}
  />
);

export const statusFilters = {
  Request: "Requested",
  Assign: "Assigned",

  // pending: 'Pending',
  // delivered: 'Delivered',
  // rto: 'RTO',
  // notPicked: 'Not Picked',
  // lost: 'Lost',
};

const PackageAssignList = () => {
  const [popupOpen, setpopupOpen] = useState(false);
  const getToday = () => {
    const d = new Date();
    d.setMinutes(d.getMinutes() - d.getTimezoneOffset());
    return d.toISOString().split("T")[0];
  };

  const today = getToday();

  const [page, setPage] = useState(1);
  const [pins, setPin] = useState("");
  const [bal, setBal] = useState("");
  const [rowsPerPage, setRowsPerPage] = useState(10);
  const [resetKey, setResetKey] = useState(false);
  const [totalData, setTotalData] = useState(0);
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [selectedRequestPackage, setSelectedRequestPackage] = useState();
  const [packageList, setPackageList] = useState([]);
  const [selectedPackage, setSelectedPackage] = useState(null);

  const defaultFilters = {
    startDate: "",
    endDate: "",
    status: "Request",
  };

  const [filters, setFilters] = useState(defaultFilters);

  const handleOpenPopup = (value) => {
    setpopupOpen(true);
    setSelectedRequestPackage(value);
  };

  const handleClosePopup = () => {
    setpopupOpen(false);
    setSelectedRequestPackage(null);
    setSelectedPackage(null);
    setBal("");
    setPin("");
  };

  const handleChangePage = (_, value) => {
    setPage(value);
  };
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(Number(event.target.value));
    setPage(1); // reset to first page when rowsPerPage changes
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFilters((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {
    if (!filters.startDate || !filters.endDate) {
      message.error("Please select start and end date");
      return;
    }

    if (filters.startDate > filters.endDate) {
      message.error("Start date cannot be greater than end date");
      return;
    }

    setPage(1);
    setResetKey((pre) => !pre);
  };

  const handleReset = () => {
    setFilters(defaultFilters);
    setActiveStatus("Request");
    setPage(1);
    setRowsPerPage(10);
    setResetKey((pre) => !pre);
  };

  const [activeStatus, setActiveStatus] = useState("Request");

  const handleStatusChange = (_, newValue) => {
    setActiveStatus(newValue);

    setFilters({
      startDate: "",
      endDate: "",
      status: newValue,
    });

    setPage(1);
    setRowsPerPage(10);
    setResetKey((pre) => !pre);
  };

  const getList = async () => {
    if (!filters.startDate || !filters.endDate) {
      setData([]);
      setTotalData(0);
      return;
    }

    let body = {
      req_type: filters.status,
      page,
      count: rowsPerPage,
      start_date: filters.startDate,
      end_date: filters.endDate,
    };

    try {
      let res = await getAsignReqPackage(body);

      if (res?.data?.statusCode === 200) {
        setData(res?.data?.data?.items || []);
        setTotalData(res?.data?.data?.totalCount || 0);
      } else {
        setData([]);
        setTotalData(0);
      }
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getList();
  }, [resetKey, page, rowsPerPage]);

  const totalPages = Math.ceil(totalData / rowsPerPage);

  const assignPackage = async () => {
    if (!selectedPackage?.price_info?._id || !bal?.trim()) {
      alert("All fields are required");
      return;
    }

    setLoading(true);
    let body = {
      package_id: selectedRequestPackage?.package_id,
      tpin: pins,
      price: selectedPackage?.price_info?._id,
      merchant_id: selectedRequestPackage?.merchant_id,
      merchantPurchasedAmt: bal || 0,
    };
    try {
      let res = await AsignPackageToRetailer(body);
      if (res?.data?.statusCode === "200") {
        handleClosePopup();
      }
      toastMessage(res?.data?.error, res?.data?.message);
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  const getPackage = async () => {
    try {
      const res = await DistributorPurchasePackageList(
        selectedRequestPackage?.package_id,
      );
      setPackageList(res?.data?.data || []);
    } catch (error) {}
  };

  useEffect(() => {
    if (popupOpen) {
      getPackage();
    }
  }, [popupOpen]);

  return (
    <>
      {loading && <Loader />}
      <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",

          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
        <Typography
          variant="h6"
          sx={{
            fontWeight: "600",
            fontSize: "24px",
            color: "var(--bs--primary2)",
            textAlign: "center",
          }}
        >
          Package Assign
        </Typography>
      </Paper>

      <div className="card">
        <div className="card-header"> {/* <span>Filter</span> */}</div>
        <div className="card-body">
          <form>
            <div className="form-row" style={{ alignItems: "end" }}>
              <div className="form-group col-md-4">
                <label>Start Date</label>
                <input
                  type="date"
                  name="startDate"
                  className="form-control input-white-bg"
                  value={filters.startDate}
                  onChange={handleChange}
                  max={today}
                />
              </div>
              <div className="form-group col-md-4">
                <label>End Date</label>
                <input
                  type="date"
                  name="endDate"
                  className="form-control input-white-bg"
                  value={filters.endDate}
                  onChange={handleChange}
                  max={today}
                  min={filters.startDate || undefined}
                />
              </div>

              <div className="form-group col-md-12 col-lg-12">
                <div>
                  <button
                    type="button"
                    className="btn mr-3 text-white"
                    style={{ backgroundColor: "#6C757D" }}
                    onClick={handleSubmit}
                  >
                    Search
                  </button>
                  <button
                    type="button"
                    className="btn btn-light"
                    onClick={handleReset}
                  >
                    Reset
                  </button>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
      <Box
        sx={{ width: "100%", borderBottom: 1, borderColor: "divider", mt: 2 }}
      >
        <Tabs
          value={activeStatus}
          onChange={handleStatusChange}
          variant="scrollable"
          scrollButtons="auto"
          aria-label="Order Status Tabs"
          sx={{
            "& .MuiTab-root": {
              textTransform: "none",
              fontWeight: 500,
              minWidth: 120,
              color: "#555",
              "&.Mui-selected": {
                color: "#000000",
                fontWeight: "bold",
              },
            },
            "& .MuiTabs-indicator": {
              backgroundColor: "#000000",
            },
          }}
        >
          {Object.entries(statusFilters).map(([key, label]) => (
            <Tab key={key} label={label} value={key} />
          ))}
        </Tabs>
      </Box>
      <Paper
        sx={{
          padding: 3,
          borderRadius: 2,
          // border: "1px solid #ccc",
          marginTop: 4,
          // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
          boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
        }}
      >
        <Box sx={{ padding: 1 }}>
          <TableContainer component={Box} elevation={3}>
            <Table>
              <TableHead sx={{ backgroundColor: "#f5f5f5" }}>
                {/* {renderTableHeader()} */}
                <TableRow>
                  <TableCell>
                    <strong>S.No.</strong>
                  </TableCell>
                  {activeStatus === "Request" ? (
                    <TableCell>
                      <strong>Requested Date</strong>
                    </TableCell>
                  ) : (
                    <TableCell>
                      <strong>Assign Date</strong>
                    </TableCell>
                  )}

                  <TableCell>
                    <strong>Package Name</strong>
                  </TableCell>
                  <TableCell>
                    <strong>Marchant Name</strong>
                  </TableCell>
                  <TableCell>
                    <strong>Marchant Code</strong>
                  </TableCell>
                  {/* <TableCell><strong>Sale Price</strong></TableCell> */}
                  {/* <TableCell><strong>MRP</strong></TableCell> */}
                  {/* <TableCell><strong>Qty</strong></TableCell> */}
                  {/* <TableCell><strong>Address</strong></TableCell> */}

                  {activeStatus === "Request" && (
                    <TableCell>
                      <strong>Action</strong>
                    </TableCell>
                  )}
                </TableRow>
              </TableHead>

              {/* {renderTableContent()} */}
              <TableBody>
                {data?.map((item, index) => (
                  <TableRow key={index} hover>
                    <TableCell sx={{ color: "var(--bs--primary2)" }}>
                      {index + 1 + (page - 1) * rowsPerPage}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs--primary2)" }}>
                      {item?.request_date || item?.assign_date}{" "}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs--primary2)" }}>
                      {item?.package_name}
                    </TableCell>
                    {/* <TableCell sx={{color:'var(--bs-nav)'}}>14</TableCell>
                <TableCell sx={{color:'var(--bs-nav)'}}>15</TableCell> */}
                    <TableCell sx={{ color: "var(--bs--primary2)" }}>
                      {item?.merchant_name}
                    </TableCell>
                    <TableCell sx={{ color: "var(--bs--primary2)" }}>
                      {item?.merchant_refer_id}
                    </TableCell>
                    {activeStatus === "Request" && (
                      <TableCell>
                        <Button
                          variant="contained"
                          color="success"
                          onClick={() => handleOpenPopup(item)}
                          sx={{
                            borderColor: "var(--bs--primary2)", // Custom colors

                            backgroundColor: "var(--bs-primary1)", // Custom colors
                            color: "var(--bs--primary2)",
                          }}
                        >
                          Assign
                        </Button>
                      </TableCell>
                    )}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>

          {/* Pagination + Rows Per Page */}
          <Box
            display="flex"
            justifyContent="space-between"
            alignItems="center"
            mt={2}
            flexWrap="wrap"
            gap={2}
          >
            <Box display="flex" alignItems="center" gap={2}>
              <FormControl size="small" sx={{ width: "150px" }}>
                <InputLabel>Rows per page</InputLabel>
                <Select
                  value={rowsPerPage}
                  onChange={handleChangeRowsPerPage}
                  label="Rows per page"
                  sx={{
                    fontSize: "14px",
                    "& .MuiOutlinedInput-notchedOutline": {
                      borderColor: "#ccc",
                    },
                    "&:hover .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                    "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                      borderColor: "var(--bs-primary1)",
                    },
                  }}
                >
                  {[5, 10, 25, 50].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>

              {/* Total Entries Count */}
              <Typography
                variant="body2"
                sx={{ fontSize: "14px", color: "#555" }}
              >
                Total Entries: {totalData}
              </Typography>
            </Box>

            <Pagination
              count={totalPages}
              page={page}
              onChange={handleChangePage}
              // color="primary"
              shape="rounded"
              showFirstButton
              showLastButton
              sx={{
                "& .MuiPaginationItem-root": {
                  color: "var(--bs--primary2)",
                  borderRadius: "8px",
                },
                "& .MuiPaginationItem-root.Mui-selected": {
                  backgroundColor: "var(--bs-primary1)",
                  color: "var(--bs--primary2)",
                  fontWeight: "bold",
                  "&:hover": {
                    backgroundColor: "var(--bs-primary3)",
                  },
                },
              }}
            />
          </Box>
        </Box>
      </Paper>

      <Dialog
        open={popupOpen}
        onClose={() => setpopupOpen(false)}
        PaperProps={{
          sx: () => ({
            borderRadius: 4,
            paddingY: 2,
            background: "rgba(255,255,255,0.96)",
            backdropFilter: "blur(6px)",
            border: "1px solid rgba(0,0,0,0.08)",
            boxShadow: "0 8px 32px rgba(0, 0, 0, 0.2)",
            width: "100%",
            maxWidth: 450,
            //  overflow: tabs?.length === 0 ? 'hidden' : 'auto',
          }),
        }}
      >
        <DialogTitle
          sx={{
            color: "var(--bs-primary1)",
            fontWeight: 600,
            textAlign: "center",
            fontSize: "1.0rem",
          }}
        >
          Assign Package to Retailer
        </DialogTitle>

        <DialogContent sx={{ textAlign: "center" }}>
          {/* <Typography sx={{ color: 'var(--bs-nav)', mb: 0 }} fontSize={14}>
                    Please enter your 4-digit TPIN to continue.
                  </Typography> */}
          {/* <Box sx={{marginY:2}}> */}

          <DetailRow
            label="Package Name"
            value={selectedRequestPackage?.package_name}
          />
          <DetailRow
            label="Marchant Name"
            value={selectedRequestPackage?.merchant_name}
          />
          <DetailRow
            label="Marchant Refer Id"
            value={selectedRequestPackage?.merchant_refer_id}
          />
          {/* </Box> */}
        </DialogContent>
        <Box
          sx={{
            px: 3,
            pt: 2,
            display: "flex",
            flexDirection: "column",
            gap: 2,
          }}
        >
          <Box sx={{ maxWidth: 380, width: "100%", mb: 2 }}>
            {/* <label>Select Package</label> */}
            <Autocomplete
              options={packageList}
              getOptionLabel={(option) =>
                `${option?.single_unit_amt} Rs , ${option?.price_info.duration}${option?.price_info?.duration_type}, ${option?.count} Count` ||
                ""
              }
              value={selectedPackage ?? null}
              size="small"
              onChange={(e, newVal) => setSelectedPackage(newVal)}
              renderOption={(props, option) => (
                <li {...props} key={option._id}>
                  <Box sx={{ fontSize: "13px", color: "var(--bs-nav)" }}>
                    {`${option?.single_unit_amt} Rs , ${option?.price_info.duration}${option?.price_info?.duration_type}, ${option?.count} Count` ||
                      ""}
                  </Box>
                </li>
              )}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Select Package"
                  sx={dialogInputSx}
                />
              )}
            />
          </Box>
          <Box sx={{ maxWidth: 380, width: "100%" }}>
            <TextField
              label="Price"
              name="prices"
              type="number"
              value={bal || ""}
              onChange={(e) => {
                const value = e.target.value;
                const pattern = /^(?:\d{0,4}(?:\.\d*)?)?$/;

                if (pattern.test(value)) {
                  setBal(value);
                }
              }}
              sx={dialogInputSx}
              autoComplete="off"
              inputProps={{ inputMode: "numeric", maxLength: 10 }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <CurrencyRupeeIcon />
                  </InputAdornment>
                ),
              }}
              fullWidth
            />
          </Box>
          <Box sx={{ mt: 2, maxWidth: 380, width: "100%" }}>
            <CustomTextField
              label="TPIN"
              type="password"
              name="usertpin"
              placeholder="Enter TPin"
              value={pins || ""}
              InputLabelProps={{ shrink: true }}
              onChange={(e) => {
                const value = e.target.value;
                if (/^\d{0,4}$/.test(value)) {
                  setPin(value);
                }
              }}
              autoComplete="off"
              inputProps={{ inputMode: "numeric", maxLength: 4 }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <LockIcon />
                  </InputAdornment>
                ),
              }}
              // sx={{ mt: 2 }}
              fullWidth
            />
          </Box>
        </Box>
        <DialogActions sx={{ justifyContent: "center", mt: 2 }}>
          <Box
            sx={{
              display: "flex",
              width: "100%",
              justifyContent: "space-between",
              px: 3, // optional padding on sides
            }}
          >
            <Box>
              <Button
                onClick={handleClosePopup}
                color="error"
                variant="outlined"
                sx={{
                  borderRadius: 2,
                  px: 4,
                  width: "150px",
                }}
              >
                Cancel
              </Button>
            </Box>

            <Box>
              <Button
                onClick={() => {
                  if (pins?.length < 4) {
                    toastMessage(true, "Please enter 4 digit TPIN");
                    return;
                  }
                  assignPackage();
                }}
                disabled={loading}
                color="primary"
                variant="contained"
                sx={{
                  backgroundColor: "var(--bs-primary1)",
                  borderRadius: 2,
                  px: 4,
                  width: "150px",

                  "&:hover": {
                    backgroundColor: "var(--bs-primary1)",
                    opacity: 0.9,
                  },
                }}
              >
                Submit
              </Button>
            </Box>
          </Box>
        </DialogActions>
      </Dialog>
    </>
  );
};

export default PackageAssignList;
